import {Box} from '@chakra-ui/react';

function FlagImg({ lang }: { lang: string }) {

    return (
        <Box>
            {lang === 'en' && <img src="data:image/png;base64," />}
            {lang === 'ru' && <img src="data:image/png;base64," />}
            {lang === 'fr' && <img src="data:image/png;base64," />}
        </Box>
    );
}

export default FlagImg;